/*==============================================================
# Image Map
==============================================================*/

.image-map {
	position: relative;
	display: block;
	height: 100%;
	width: 100%;
	margin: 0 auto;
	@include breakFirst(large) {
			height: 0;
			padding-bottom: 56.245%;
	}
	img {
		@include breakFirst(large) {
			min-width: 100%;
			height: 100%;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
	}
	.map-target {
		position: absolute;
		font-size: 12px;
		span {
			position: relative;
			z-index: 1;
			&:last-child {
				left: -10px;
			}
		}
		.target-text {
			background: $white;
			color: $black;
			vertical-align: middle;
			display: inline-block;
			padding: 5px 15px;
			z-index: 0;
		}
	}
	a {
		transition: .3s $cbTrans all;
		transform: translate(0%, 15%);
		opacity: 0;
		&:nth-of-type(1) {
			transition-delay: .5s;
		}
		&:nth-child(2) {
			transition-delay: .7s;
		}
		&:nth-child(3) {
			transition-delay: .9s;
		}
		&:nth-child(4) {
			transition-delay: 1s;
		}
		&:nth-child(5) {
			transition-delay: 1.2s;
		}
		&:nth-child(6) {
			transition-delay: 1.4s;
		}
		&:nth-child(7) {
			transition-delay: 1.6s;
		}
		&:nth-child(8) {
			transition-delay: 1.8s;
		}
		&:nth-child(9) {
			transition-delay: 2s;
		}
		&:nth-child(10) {
			transition-delay: 2.2s;
		}
		&:nth-child(11) {
			transition-delay: 2.4s;
		}
		&:nth-child(12) {
			transition-delay: 2.6s;
		}
	}
	&.kitchen-map {
		.target-one {
			top: 20%;
			top: calc(20% - 45px);
			left: 20%;
		}
		.target-two {
			top: 30%;
			top: calc(30% - 45px);
			left: 59%;
		}
		.target-three {
			top: 40%;
			top: calc(52% - 45px);
			left: 40%;
		}
		.target-four {
			top: 80%;
			top: calc(80% - 45px);
			left: 30%;
		}
	}
	&.great-room-map {
		.target-one {
			top: 26%;
			left: 50%;
		}
		.target-two {
			top: 29%;
			right: 20%;
		}
		.target-three {
			top: 55%;
			left: 70%;
		}
		.target-four {
			top: 39%;
			left: 20%;
		}
	}
	&.living-room-map {
		.target-one {
			top: 26%;
			left: 30%;
		}
		.target-two {
			top: 39%;
			right: 26%;
		}
		.target-three {
			top: 15%;
			right: 5%;
		}
	}
	&.bathroom-map {
		.target-one {
			top: 60%;
			top: calc(60% - 45px);
			left: 15%;
		}
		.target-two {
			top: 65%;
			top: calc(65% - 45px);
			left: 60%;
		}
		.target-three {
			top: 35%;
			top: calc(35% - 45px);
			left: 40%;
		}
	}
	&.exterior-map {
		.target-one {
			top: 26%;
			left: 50%;
		}
		.target-two {
			top: 29%;
			left: 10%;
		}
		.target-three {
			top: 45%;
			left: 70%;
		}
	}
}
.current-section {
	.slick-current {
		.image-map {
			a {
				opacity: 1;
				transform: translate(0%, 0%);
			}
		}
	}
}
.disable-section {
	.slick-current {
		.image-map {
			a {
				opacity: 0;
				transform: translate(0%, 15%);
			}
		}
	}
}