/* ==============================================================
# Helpers
============================================================== */

// Clear inner floats
// --------------------------------------------------

.cf:before,
.cf:after {
		content:"";
		display:table;
}
.cf:after {
		clear:both;
}
.cf{
	*zoom: 1;
}

/*--------------------------------------------------------------
# Accessibility
--------------------------------------------------------------*/

// Text meant only for screen readers
// --------------------------------------------------

.srt-only {
	clip: rect(1px, 1px, 1px, 1px);
	position: absolute !important;
	height: 1px;
	width: 1px;
	overflow: hidden;
	word-wrap: normal !important; /* Many screen reader and browser combinations announce broken words as they would appear visually. */
}
.rt-only:focus {
	background-color: #f1f1f1;
	border-radius: 3px;
	box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
	clip: auto !important;
	color: #21759b;
	display: block;
	font-size: 14px;
	font-size: 0.875rem;
	font-weight: bold;
	height: auto;
	left: 5px;
	line-height: normal;
	padding: 15px 23px 14px;
	text-decoration: none;
	top: 5px;
	width: auto;
	z-index: 100000; /* Above WP toolbar. */
}

// Do not show the outline on the skip link target
// --------------------------------------------------

#content[tabindex="-1"]:focus {
	outline: 0;
}

/*--------------------------------------------------------------
# Alignments
--------------------------------------------------------------*/

.alignleft {
	display: inline;
	float: left;
	margin-right: 1.5em;
}
.alignright {
	display: inline;
	float: right;
	margin-left: 1.5em;
}
.aligncenter {
	clear: both;
	display: block;
	margin-left: auto;
	margin-right: auto;
}

/*--------------------------------------------------------------
# Text alignment helpers
 --------------------------------------------------------------*/

.text-left {
	text-align: left !important;
}
.text-center {
	text-align: center !important;
}
.text-right {
	text-align: right !important;
}

/*--------------------------------------------------------------
 * Vertically center content
 --------------------------------------------------------------*/

.center-wrapper {
	display: table;
	height: 100%;
	width: 100%;
}
.center-content {
	display: table-cell;
	height: 100%;
	width: 100%;
	vertical-align: middle;
}